.monster-list-container {
    display: flex;
    justify-content: center;
    margin: 20px;
}

.monster-table {
    width: 100%; /* Default width (100%) for smaller screens */
    max-width: 1200px; /* Maximum width for larger screens */
    margin: 0 auto; /* Center the table */
    border-collapse: collapse;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

/* Adjust the table width to 50% on larger screens */
@media screen and (min-width: 768px) {
    .monster-table {
        width: 50%; /* Table takes up 50% of the screen width */
    }
}

.monster-table th, .monster-table td {
    padding: 10px;
    border: 1px solid #ddd;
}

.monster-table th {
    background-color: #4CAF50;
    color: white;
}

.monster-table th:first-of-type {
    width: 50%;
}

.monster-table tbody tr:nth-child(even) {
    background-color: #f2f2f2;
}

.monster-table tbody tr:hover {
    background-color: #e9f7e3;
}

.monster-header {
    text-align: center; /* Align the header text to the left */
}
