.monster-row {
    text-align: center;
    border-bottom: 1px solid #ccc;
    transition: background 0.2s;
}

.monster-row:hover {
    background: #f5f5f5;
}

/* Container for splitting the icon and name in the Monster column */
.monster-info {
    display: flex;
    justify-content: space-between; /* Space between icon and name */
    align-items: center;
}

.icon-container {
    flex: 1;
}

.monster-icon {
    width: 50px;
    height: 50px;
    border-radius: 5px;
}

.name-container {
    flex: 2; /* Give more space for the name */
    text-align: center; /* Center the name */
}

/* Hide the name on small screens (e.g., phones) */
.monster-name {
    font-weight: bold;
}

@media screen and (max-width: 600px) {
    /* For small screens, hide the monster's name */
    .name-container {
        display: none;
    }

    /* Optionally adjust the icon size for smaller screens */
    .monster-icon {
        width: 40px;
        height: 40px;
    }
}

/* Center the checkboxes in their columns */
.checkbox-cell {
    text-align: center;
}

input[type="checkbox"] {
    cursor: pointer;
}
